<template>
    <div class="container mx-auto p-4">


        <!-- 使用全局注册的 BackHome 组件 -->
        <BackHome />
        <!-- Change Password Form -->
        <form @submit.prevent="changePassword" class="card bg-base-100 shadow-xl p-6">
            <div class="form-control mb-4">
                <label class="label">Current Password</label>
                <input v-model="formData.currentPassword" type="password" placeholder="Enter your current password"
                    class="input input-bordered" required />
            </div>
            <div class="form-control mb-4">
                <label class="label">New Password</label>
                <input v-model="formData.newPassword" type="password" placeholder="Enter a new password"
                    class="input input-bordered" required />
            </div>
            <div class="form-control mb-4">
                <label class="label">Confirm New Password</label>
                <input v-model="formData.confirmNewPassword" type="password" placeholder="Confirm your new password"
                    class="input input-bordered" required />
            </div>
            <button type="submit" class="btn btn-primary mt-4">Change Password</button>
        </form>
    </div>
</template>

<script>

export default {
    name: 'Password',
    data() {
        return {
            formData: {
                currentPassword: '',
                newPassword: '',
                confirmNewPassword: '',
            },
        };
    },
    components: {  },
    methods: {
        async changePassword() {
            if (this.formData.newPassword !== this.formData.confirmNewPassword) {
                alert('New passwords do not match!');
                return;
            }

            // Assuming supabase is already initialized
            const { data, error } = await supabase
                .from('users')
                .update({
                    password: this.formData.newPassword,
                })
                .eq('id', 'your-user-id');

            if (error) {
                console.error('Error changing password:', error);
                alert('Error changing password. Please try again.');
            } else {
                alert('Password changed successfully!');
                this.$router.push({ name: 'Blog' });
            }
        },
    },
};
</script>
